<template>


<div>


 <dheader></dheader>
  <report-view></report-view>
  </div>
</template>

<script>

  import GatewayService from './../services/gatewayServices'
  import dheader from './common/dheader'
  import ReportView from './Reports'



  export default {
    components: {
      ReportView,
      dheader
    },
    name: "Dashboard",
        data(){
          return{
            isAuthorized : false,
            role: false,
            roleMenu: {
              admin: false,
              manager: false
            }
          }
        },

        async mounted () {
        try {
             this.isAuthorized = await GatewayService.isAuthorize()
             if(this.isAuthorized){
               // Get username from cookie
               let user = this.$cookie.get('username');
               console.log(`Welcome ${user}`)
             } else {
               this.$router.push({
                 name : 'Login'
               })
             }

        } catch (err){
          this.$router.push({
            name : 'Login'
          })
        }
      this.status = this.isAuthorized
      },

      methods : {
          home : function (){
            this.$router.push({
              name : 'Login'
            })
          }
      }


    }
</script>

