import api from './api'

export default {
  login(data){
    return api.execute(`post`,`/login`, data)
  },
  logout(){
    return api.execute(`get`,`/logout`)
  },
  isAuthorize(){
    return api.execute(`get`,`/dashboard`)
  },
  signup(data){
    return api.execute(`post`, '/signup', data)
  },
  findRole(user){
    return api.execute('get', `role/${user}`)
  }

}
