<script>
    import SectionService from './../services/sectionServices'
    export default {
        name: "Section",
        data: function () {
            return {
                sections: [],
                sectionId: '',
                sectionAScore: '',
                sectionTitle: '',
                fields: [
                    {key: 'sid', label: 'SID'},
                    {key: 'ascore', label: 'Avarage Score'},
                    {key: 'title', label: 'Title'},
                    {key: 'actions', label: 'Actions'}
                ],
                }
        },
        async mounted() {
            await this.init()
        },
        methods: {
            init: async function () {
                try {
                    let result =await SectionService.getAllSections()
                    console.log(result.message)
                    this.sections = result.message
                } catch (e) {
                    console.error("Error in getting section")
                    console.error(e)
                }

            },
            addNewSection: async function(){
               let payload = {}
                payload.sid = this.sectionId
                payload.ascore = this.sectionAScore
                payload.title = this.sectionTitle
                try {
                     let res =await SectionService.insertSection(payload)
                     if(res.response) {
                        console.log("Section Inserted")
                     }
                } catch (e) {
                    console.error("error in saving new section")
                    console.error(e)
                }
            },
        }
    }
</script>

