<template>
    <div>
        <b-table striped hover :items="records" :fields="fields">

            <template v-slot:cell(answers)="row">
                <b-button size="sm" @click="row.toggleDetails" class="mr-2">
                    {{ row.detailsShowing ? 'Hide' : 'Show'}} Answers
                </b-button>

                <!-- As `row.showDetails` is one-way, we call the toggleDetails function on @change -->
            </template>
            <template v-slot:row-details="row">
                <b-card>
                    <!--{{row.item.answers[0].aid || row.item.answers[1].aid}}-->
                    <b-button class="m-1" v-for="item in sections" :key="item[Object.keys(item)]" @click="test(item[Object.keys(item)], row.item.answers[0].aid || row.item.answers[1].aid)">{{Object.keys(item)[0]}}</b-button>
                    <b-table striped hover :items="answers" :fields="ansFields">
                    </b-table>
                    <b-button size="sm"  @click="row.toggleDetails" variant="danger">Hide Details</b-button>
                </b-card>
            </template>
        </b-table>
    </div>

</template>

<script>
    import ReportService from './../services/ReportServices'
    // import AnswerService from './../services/AnswerServices'
    // import QuetionService from './../services/QuetionServices'
    // import SectionService from './../services/sectionServices'
    export default {
        name: "Reports",
        data(){
            return {
                answers:[],
                allanswers: [],
                sections: [],
                records: [],
                ansFields:["quetion", "answer"],
                fields: ["email",       {
                    key: 'updatedAt',
                    label: 'Submitted On'}, "answers"]
            }
        },
        async mounted(){
           await this.getReports()
        },
        methods: {
            test: function (index, answerArray){
                console.log(answerArray)

                let temp = this.allanswers[index].quetions.map(e =>{
                    console.log(e)
                    let answer = "notGiven"
                    for(let ans of e.answers){
                        if(answerArray.includes(ans.id)){
                            answer = ans.answer
                            break
                        }
                    }
                  return  {quetion:e.quetion, answer: answer }
                })
                this.answers = temp
                return true
            },
            getReports:async function () {
                try {
                    let response = await ReportService.getAllUsersData()
                    console.log("ANSWERS" + JSON.stringify(response))

                    this.records = response.message
                } catch (e) {
                    console.error("Error in getting report api")
                    console.error(e)
                }
                try {
                    let index = 0
                    let response = await ReportService.getAllAnswers()
                    this.allanswers = response.data

                    this.sections = response.data.map((e)=>{
                        return {[e.title]: index++}
                    })

                    console.log(this.sections)

                } catch (e) {
                    console.error("Error in getting answers api")
                    console.error(e)
                }
            }
        }
    }
</script>

