<template>
<div>
    <b-navbar toggleable="md" variant="info">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand href="#"> <a class="navbar-item"></a></b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav class="ml-auto">
          <b-btn size="lg" variant="outline-warning" class="float-right mr-sm-2" @click="logout">
            logout
          </b-btn>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
</div>


</template>

<script>
  import GatewayServices from '../../services/gatewayServices'

  export default {
    name: "dheader",

    methods: {
      logout: async function () {

        try {
          await GatewayServices.logout()
          // To delete a cookie use
          // this.$cookie.delete('username');
          this.$router.push({
            name: "Gateway"
          });

        } catch (err) {
          console.log('Error in logging out')
          console.log(err)
        }

      }
    }
  }
</script>

