<script>
  import GatewayService from '../services/gatewayServices'

  export default {
    name: "Signup",

    data() {

      return {
        alert: false,
        model: {},
        confirmPwd: "",
        loading: "",
        status: "",
        roleOptions: [],
      };
    },
    async mounted(){
    },
    methods: {

      signup: async function () {
        let payload = this.model;
        console.log('SignUp Call');
        if (this.model.password == this.confirmPwd) {
          var serverRes = false;
          try {
            serverRes = await GatewayService.signup(payload);
            console.log(`Signup Status : ${JSON.stringify(serverRes)}`);
            if (serverRes.result) {
              this.$router.push({
                name: "Login"
              });
            } else {
              console.log(serverRes.message)
              this.makeToast('danger', serverRes.message.signupMessage[0])
            }
          } catch (err) {
              this.makeToast('danger', "connection issue");
          }
        } else {
            this.makeToast('danger', "password mismatch!");
        }
      },
      makeToast(variant = null, message) {
        this.$bvToast.toast(message, {
          title: `Message`,
          variant: variant,
          solid: true
        })
      },
      home: function () {
        this.$router.push({
          name: 'Gateway'
        })
      }
    }
  }
</script>

