<!--suppress ALL -->
<template >
  <div>
  <b-container>
    <b-row class="justify-content-md-center ">
      <b-col col lg="2"></b-col>
      <b-col cols="12" md="auto">
        <div class="text-center">
      <div class="mt-5">
        <img src="../assets/Kearney-Logo-Slate.png" width="25%" />
      </div>
        </div></b-col>
      <b-col col lg="2"></b-col>
      <!--<img class="mb-4" src="../assets/Kearney-Logo-Slate.png" alt="" width="72" height="72">-->
    </b-row>
    <b-row class="justify-content-md-center mt-5">
      <b-col col lg="2"></b-col>
      <b-col cols="12" md="4">
        <div class="text-center">
          <b-card>
            <form @submit.prevent="login">
              <div class="form-group text-left">
                <label>Email</label>
                <input type="text" class="form-control" v-model="model.email" :required=true >
              </div>
              <div class="form-group text-left">
                <label>Password</label>
                <input type="password" class="form-control" v-model="model.password" :required=true>
              </div>

              <button type="submit" class="btn btn-warning btn-lg">Login</button>
            </form>
          </b-card>

          <hr>

          <p>Need an account? <b-button variant="dark" @click="signup">Signup</b-button></p>

        </div>

      </b-col>
      <b-col col lg="2"></b-col>
    </b-row>

    <b-row>
    <b-col>

    </b-col>
    </b-row>
  </b-container>
  </div>
</template>

<script>
  import GatewayService from '../services/gatewayServices'

  export default {
    name: "Login",
    data() {
      return {
        alert: false,
        model: {
          email: "",
          password: ""
        },

        loading: "",
        status: ""
      };
    },
    methods: {
      signup: function () {
        this.$router.push({
          name: "Signup"
        });
      },
      makeToast(variant = null, message) {
        this.$bvToast.toast(message, {
          title: `Message`,
          variant: variant,
          solid: true
        })
      },
      async login() {
        let serverRes = false
        try {
          serverRes = await GatewayService.login(this.model)
          console.log("SERVER LOGIN RESPONSE: " + JSON.stringify(serverRes))
          if (serverRes.success) {
            this.$root.$data.isAuthenticated = true
            this.$root.$data.user = this.model.email
            this.$cookie.set('username',this.model.email , 1);
            this.$router.push({
              name: "Dashboard"
            });
          } else {
            this.alert = true
            this.status = 'Unauthorized'
          }
        } catch (err) {
          this.makeToast('danger', "connection issue")
        }
      }
    }
  }
</script>
