import axios from 'axios'
let mainapiuri = `https://serene-lowlands-70198.herokuapp.com/api/`
console.log(mainapiuri)

var client = axios.create({
  baseURL: mainapiuri,
  timeout: (1000 * 5 * 60)
});

export default {
  async execute(method, uri, data){
    try {
      let req =await client({
        method,
        url : uri,
        withCredentials : true,
        data : data
      })

      return req.data
    } catch (err) {
      console.log('Error in getting Server uri')
      throw (err)
    }
  }
}
